

import { BrowserRouter , HashRouter, useNavigate, Routes, Route, Link } from 'react-router-dom';
import React, { Component, Fragment } from 'react';

function AppWrapper(props) {

	let { children } = props;
	if (!Array.isArray(children))
		children = [ children ];

	children = children.map((child, i) => {
		const { path, component:Component=Fragment, key=i } = (child.props || {});
		return <Route key={key} exact path={path} element={<Component />} />
	});


	let Router;

	switch (props.router) {

		case 'hash':
			Router = HashRouter;
			break;

		default:
			Router = BrowserRouter;

	}

	return (
	<Router>

		<Wrapper>

			<Routes>
				{children}
			</Routes>

		</Wrapper>
	</Router>
	); 
}


function Wrapper(props) {

  window.App.redirect = useNavigate();

  return <Fragment>
    {props.children}
  </Fragment>
}


function AppRoute(props) {

  return <Fragment />

}



window.App = {};

export default AppWrapper;

export {
  AppRoute as Route,
  Link
}